import React, { useState } from 'react';
import { navigate } from 'gatsby';
import '../componets/log-in/login.css';
import { FiArrowRight } from 'react-icons/fi';

const i18n = {
  EN: {
    brandTitle: 'TINH HOA VIỆT',
    tagline: "Where Vietnamese Excellence Meets Global Recognition",
    features: ['Premium Vietnamese Heritage', 'Global Excellence Standards', 'Elite Community Access'],
    title: 'Reset Password',
    subtitle: 'Enter your email to receive reset instructions',
    email: 'Email Address',
    submit: 'Send Reset Link',
    backToLogin: 'Back to Login',
    success: 'Reset link sent! Check your email.',
    langEN: 'EN',
    langVN: 'VN'
  },
  VN: {
    brandTitle: 'TINH HOA VIỆT',
    tagline: 'Nơi tôn vinh tinh hoa Việt Nam vươn tầm quốc tế',
    features: ['Di sản Việt Nam đẳng cấp', 'Chuẩn mực xuất sắc toàn cầu', 'Cộng đồng tinh hoa'],
    title: 'Đặt lại mật khẩu',
    subtitle: 'Nhập email để nhận hướng dẫn đặt lại mật khẩu',
    email: 'Địa chỉ Email',
    submit: 'Gửi liên kết đặt lại',
    backToLogin: 'Quay lại đăng nhập',
    success: 'Đã gửi liên kết! Kiểm tra email của bạn.',
    langEN: 'EN',
    langVN: 'VN'
  }
};

const ResetPassword: React.FC = () => {
  const [email, setEmail] = useState('');
  const [language, setLanguage] = useState<'EN' | 'VN'>('EN');
  const [isSubmitted, setIsSubmitted] = useState(false);
  const t = i18n[language];

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitted(true);
    // Simulate API call
    setTimeout(() => {
      navigate('/sign-in');
    }, 2000);
  };

  return (
    <div className="auth-shell">
      <aside className="brand-side" aria-hidden>
        <div className="brand-sheen" />
        <div className="brand-pattern" />
        <div className="brand-wrap">
          <h1 className="brand-name">{t.brandTitle}</h1>
          <p className="brand-tag">{t.tagline}</p>
          <ul className="brand-features">
            {t.features.map((f, i) => (
              <li className="feature" key={i}><span className="tick">✓</span><span>{f}</span></li>
            ))}
          </ul>
        </div>
      </aside>

      <main className="form-side">
        <header className="form-top">
          <div className="lang-switch">
            <button
              className={`lang ${language === 'EN' ? 'active' : ''}`}
              onClick={() => setLanguage('EN')}
            >{t.langEN}</button>
            <span className="mid">|</span>
            <button
              className={`lang ${language === 'VN' ? 'active' : ''}`}
              onClick={() => setLanguage('VN')}
            >{t.langVN}</button>
          </div>
        </header>

        <section className="form-wrap">
          <div className="intro">
            <h2 className="title">{t.title}</h2>
            <p className="subtitle">{t.subtitle}</p>
          </div>

          {isSubmitted ? (
            <div className="success-message" style={{ 
              textAlign: 'center', 
              padding: '2rem', 
              color: '#00bcd4',
              fontSize: '1.1rem'
            }}>
              {t.success}
            </div>
          ) : (
            <form onSubmit={handleSubmit} className="auth-form">
              <div className="field">
                <label htmlFor="email">{t.email}</label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder={language === 'EN' ? 'your.email@example.com' : 'email@vi-du.com'}
                  required
                />
              </div>

              <button type="submit" className="cta">
                <span>{t.submit}</span>
                <FiArrowRight className='arrow'/>
              </button>

              <p className="switch">
                <a href="/sign-in">{t.backToLogin}</a>
              </p>
            </form>
          )}
        </section>
      </main>
    </div>
  );
};

export default ResetPassword;